@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 4%;
}


@keyframes animate {
  0% {
    background-position: -500%;
  }
  100% {
    background-position: 500%;
  }
}

.butcon{
  margin-top: 5%;
}
.buttons-bar {
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  height: 70%;
  margin-left: 2%;
  margin-right: 2%;
  padding-left: 2%;
  padding-right: 2%;
  border-radius: 50vw;
  font-size: medium;
  color: white;
  background-color: black;
  transition: 0.3s;
}

@media only screen and (max-width: 510px) {
  button {
    font-size: small;
  }
}

@media only screen and (max-width: 410px) {
  button {
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  button {
    font-size: xx-small;
  }
}

button:hover {
  font-style: bold;
  border-width: 0.3vh;
  cursor: pointer;
  transition: 0.3s;
}

.buttonStyle1:hover {
  border-color: gray;
  color: gray;
}

.buttonStyle2:hover {
  border-color: peru;
  color: gray;
}

#reset {
  opacity: 0.6;
  background-color: pink;
  color: black;
}
#reset:hover {
  opacity: 0.9;
}

button:disabled,
.buttonStyle1:disabled,
.buttonStyle2:disabled,
#reset:disabled {
  background-color: grey;
  color: white;
  border-width: 0vw;
  opacity: 1;
  cursor: not-allowed;
}

.bar-container {
  margin-top: 5%;
  width: 100%;
  height: 74%;
  margin-left: -1%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bar {
  width: 4vw;
  height: 70vh;
  margin: 0 1.5%;
  transform-style: preserve-3d;
  transform: rotateX(-20deg) rotateY(-30deg);
  background-color: rgba(254, 254, 254, 0.25);
}

.side {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(254, 254, 254, 0.25);
}

.back {
  transform: translateZ(-4vw);
}

.left {
  transform-origin: left;
  transform: rotateY(90deg);
}

.right {
  transform-origin: right;
  transform: rotateY(-90deg);
}

.top {
  height: 4vw;
  transform-origin: top;
  transform: rotateX(-90deg);
  background-color: rgba(254, 254, 254, 0.15);
}

.bottom {
  height: 4vw;
  transform-origin: top;
  transform: translateY(70vh) rotateX(-90deg);
  background-color: rgba(225, 0, 120, 0.5);
  box-shadow: 5px 5px 50px 5px rgba(225, 0, 120, 0.2);
  transition: 0.3s;
}

.color-bar {
  background-color: rgba(225, 0, 120, 0.5);
  box-shadow: 5px 5px 50px 5px rgba(225, 0, 120, 0.2);
  transition: 0.3s;
}

#text-array-size {
  color: white;
}

@media only screen and (max-width: 820px) {
  #text-array-size {
    font-size: small;
  }
}

@media only screen and (max-width: 510px) {
  #text-array-size {
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  #text-array-size {
    font-size: xx-small;
  }
}

#text-animation-speed {
  color: white;
}

@media only screen and (max-width: 820px) {
  #text-animation-speed {
    font-size: small;
  }
}

@media only screen and (max-width: 510px) {
  #text-animation-speed {
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  #text-animation-speed {
    font-size: xx-small;
  }
}

.range-slider-container {
  width: 100%;
  margin-bottom: 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50vh;
  background-image: linear-gradient(
    to bottom,
    rgba(100, 100, 250, 0.9),
    rgba(100, 100, 250, 0.5),
    rgba(100, 100, 250, 0.9)
  );
  opacity: 0.9;
}

.range-slider-container:hover {
  opacity: 1;
}

#range-slider {
  margin-top: 5%;
  height: 8%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  transition: visibility 0.3s linear, opacity 0.3s linear;
}

.column {
  width: 80%;
  /* align-self: center; */
  justify-self: center;
}

:root {
  --radius: 5.5;
  --size: 4;
}
h1{
  text-align: center;
}
.mainInfo {
  min-height: 40vh;
  background: hsl(var(--hue), 20%, 70%);
  overflow: hidden;
  margin: 0;
  padding: 0;
  background: #fcedd8;

}
[type='checkbox'] {
  position: absolute;
  left: 100%;
  opacity: 0;
  height: 0;
  width: 0;
}
#info{
  width: 90%;
}
table{
  text-align: center;
}
th{
  width: 25%;
}
.scene {
    perspective: 800px;
    transform-style: preserve-3d;
    height: 5vh;
    width: 100vw;
    display: flex;
    justify-content: left;
    margin-top: 4%;
    margin-left: 4% ;
    background: #fcedd8;
  }
  .plane {
    height: 5vmin;
    width: 5vmin;
    transform-style: preserve-3d;
    transform: rotateX(-40deg) rotateY(-45deg) rotateX(90deg);
  }
  .cube,
  .shadow {
  
    --width: var(--size);
    --height: var(--size);
    --depth: var(--size);
    height: calc(var(--depth) * 1vmin);
    width: calc(var(--width) * 1vmin);
    transform-style: preserve-3d;
    position: absolute;
    font-size: 1rem;
    animation: jump 0.8s calc(var(--d) * 1s) infinite;
    transform: translate3d(calc((var(--x) * 5.5) * 1vmin), calc((var(--y) * 5.5) * -1vmin), 0);
    transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
  }
  .cube > div:nth-of-type(1),
  .shadow > div:nth-of-type(1) {
    background: hsl(var(--hue), 60%, 50%);
    height: calc(var(--height) * 1vmin);
    width: 100%;
    transform-origin: 50% 50%;
    transform: rotateX(-90deg);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateX(-90deg) translate3d(0, 0, calc((var(--depth) / 2) * 1vmin));
  }
  .cube > div:nth-of-type(2),
  .shadow > div:nth-of-type(2) {
    background: hsl(var(--hue), 60%, 60%);
    height: calc(var(--height) * 1vmin);
    width: 100%;
    transform-origin: 50% 50%;
    transform: translate(-50%, -50%) rotateX(-90deg) rotateY(180deg) translate3d(0, 0, calc((var(--depth) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(3),
  .shadow > div:nth-of-type(3) {
    background: hsl(var(--hue), 60%, 60%);
    height: calc(var(--height) * 1vmin);
    width: calc(var(--depth) * 1vmin);
    transform: translate(-50%, -50%) rotateX(-90deg) rotateY(90deg) translate3d(0, 0, calc((var(--width) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(4),
  .shadow > div:nth-of-type(4) {
    background: hsl(var(--hue), 60%, 55%);
    height: calc(var(--height) * 1vmin);
    width: calc(var(--depth) * 1vmin);
    transform: translate(-50%, -50%) rotateX(-90deg) rotateY(-90deg) translate3d(0, 0, calc((var(--width) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(5),
  .shadow > div:nth-of-type(5) {
    background: hsl(var(--hue), 60%, 45%);
    height: calc(var(--depth) * 1vmin);
    width: calc(var(--width) * 1vmin);
    transform: translate(-50%, -50%) translate3d(0, 0, calc((var(--height) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(6),
  .shadow > div:nth-of-type(6) {
    background: hsl(var(--hue), 60%, 65%);
    height: calc(var(--depth) * 1vmin);
    width: calc(var(--width) * 1vmin);
    transform: translate(-50%, -50%) translate3d(0, 0, calc((var(--height) / 2) * -1vmin)) rotateX(180deg);
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .shadow {
    animation: none;
    background: radial-gradient(#000, transparent);
    transform: translate3d(calc((var(--x) * 5.5) * 1vmin), calc((var(--y) * 5.5) * -1vmin), calc(4 * -0.5vmin)) scale(1);
    transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    animation: shadow 0.8s calc(var(--d) * 1s) infinite;
  }
  .cube--00,
  .shadow--00 {
    background:  rgba(225, 0, 120, 0.5);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--01,
  .shadow--01 {
    background: #000;;
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
 
  .cube--02,
  .shadow--02 {
    background:#fcedd8;
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--03,
  .shadow--03 {
    background-color: rgba(255,165,0, 0.9);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--04,
  .shadow--04 {
    background-color: rgba(144,238,144, 0.9);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--05,
  .shadow--05 {
    background-color: rgba(0,0,255, 0.9);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--6,
  .shadow--6 {
    --x: 0;
    --y: 1;
    --d: -0.1;
  }
  .cube--7,
  .shadow--7 {
    --x: 1;
    --y: 1;
    --d: 0;
  }
  @keyframes jump {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * 5.5) * 1vmin), calc((var(--y) * 5.5) * -1vmin), 0);
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
    }
    15% {
      transform: translate3d(calc((var(--x) * 5.5) * 1vmin), calc((var(--y) * 5.5) * -1vmin), 3.5vmin);
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 3.5vmin);
    }
  }
  @keyframes shadow {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * 5.5) * 1vmin), calc((var(--y) * 5.5) * -1vmin), calc(4 * -0.5vmin)) scale(1);
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    }
    15% {
      transform: translate3d(calc((var(--x) * 5.5) * 1vmin), calc((var(--y) * 5.5) * -1vmin), calc(4 * -0.5vmin)) scale(0.85);
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(0.85);
      opacity: 0.2;
    }
  }
